.container {
  display: grid;
  grid-template-columns: 10% 40% 40% 10%;
  grid-template-rows: auto auto auto;
  font-family: "MuseoModerno", cursive, Sans-Serif;
}

.hero-home {
  grid-column: 2 / span 2;
  grid-row: 1;
  background: url(/PaulaSantos-bbk-module-ii/static/media/home.50582439.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 25em;
  margin: 2em 0 2em 0;
}

.hero-starter {
  grid-column: 2 / span 2;
  grid-row: 1;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/starter.21fc9d9a.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 25em;
  margin: 2em 0 0 0;
}

.hero-main {
  grid-column: 2 / span 2;
  grid-row: 1;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/main.86f6edff.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 25em;
  margin: 2em 0 0 0;
}

.hero-dessert {
  grid-column: 2 / span 2;
  grid-row: 1;
  background: url(/PaulaSantos-bbk-module-ii/static/media/dessert.8ae7bd23.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 25em;
  margin: 2em 0 0 0;
}

.hero-pies {
  grid-column: 2 / span 2;
  grid-row: 1;
  background: url(/PaulaSantos-bbk-module-ii/static/media/pie.5a78ca9d.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 25em;
  margin: 2em 0 0 0;
}

.hero-home h1,
.hero-starter h1,
.hero-main h1,
.hero-dessert h1,
.hero-pies h1 {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.6);
  /*opacity: 70%;
  text-align: left;*/
  padding: 0.5em;
}

.container-1 h2,
.container-2 h2,
.container-3 h2,
.container-4 h2 {
  font-size: 1.5em;
  text-align: left;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  /*opacity: 70%;*/
  padding: 0.4em;
}

.container-1 h2 > a,
.container-2 h2 > a,
.container-3 h2 > a,
.container-4 h2 > a {
  text-decoration: none;
  color: #fff;
}

.container-1 {
  grid-column: 2 / 3;
  grid-row: 2;
  margin: 1em 0.5em 0.5 0;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/starter-home.6f4f91c6.jpg);
  height: 14em;
}

.container-2 {
  grid-column: 3 / 4;
  grid-row: 2;
  margin: 0 0 1em 0.5em;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/main-home.b3247f51.jpg);
  height: 14em;
}

.container-3 {
  grid-column: 2 / 3;
  grid-row: 3;
  margin: 1em 0.5em 0.5 0;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/dessert-home.e1588767.jpg);
  height: 14em;
}

.container-4 {
  grid-column: 3 / 4;
  grid-row: 3;
  margin: 0 0 1em 0.5em;
  background-image: url(/PaulaSantos-bbk-module-ii/static/media/pie-home.c19f756a.jpg);
  height: 14em;
}

.ingredients {
  grid-column: 2 / 3;
  grid-row: 3;
  margin: 1em 0.5em 1em 0;
}

.ingredients h2 {
  text-align: left;
  background-color: #b5c723;
  padding: 1em;
  margin: 1em 0.5em 0 0;
}

.ingredients-list {
  text-align: left;
  background-color: #d8e7be;
  padding: 1em;
  margin: 0 0.75em 0 0;
}

.method {
  grid-column: 3 / 4;
  grid-row: 3;
  margin: 1em 0 1em 0.5em;
}

.method h2 {
  text-align: left;
  background-color: #b5c723;
  padding: 1em;
  margin: 1em 0 0 0;
}

.method-list {
  text-align: left;
  background-color: #d8e7be;
  padding: 1em;
  margin: 0 0 0 0;
}

/* Form styles */

.heading {
  grid-column: 2 / span 2;
  grid-row: 1;
  text-align: center;
  color: #800000;
  align-content: center;
  justify-content: center;
}

.form {
  grid-column: 2 / span 2;
  grid-row: 2;
  text-align: center;
  margin-bottom: 1em;
}

label {
  text-align: left;
  border-radius: 0.625em;
  font-size: 1.5em;
  line-height: 1.8;
}

input {
  border: 0.125em solid #800000;
  background: #fff;
  text-align: left;
  border-radius: 0.625em;
  font-size: 1.3em;
  line-height: 1.8;
  margin: 0.5% 0.5% 0.5% 1%;
}

textarea {
  border: 0.125em solid #800000;
  background: #fff;
  font-family: Tahoma, Geneva, sans-serif;
  text-align: left;
  border-radius: 0.625em;
  padding: 1%;
  font-size: 1.3em;
  line-height: 1.8;
  margin: 0.5% 0.5% 0.5% 1%;
}

button {
  border: solid #800000;
  background: #800000;
  color: #fff;
  font-weight: bold;
  text-align: center;
  border-radius: 0.625em;
  padding: 1%;
  font-size: 1.5em;
  line-height: 1.8;
  margin: 1.5% 1.5% 1.5% 1%;
  cursor: pointer;
}

button a {
  text-decoration: none;
  color: #fff;
}

button a:hover {
  text-decoration: underline;
  color: #fff;
}

button:active {
  text-decoration: underline;
  background: #000;
}

.content {
  grid-column: 2 / span 2;
  grid-row: 1;
  justify-content: center;
  align-content: center;
}

.content .title {
  text-align: center;
}

/* Responsive */

@media screen and (max-width: 800px) {
  /* .container {
    display: grid;
    grid-template-columns: 10% 80% 10%;
    grid-template-rows: auto auto auto;
  }
  */
  .hero-home h1,
  .hero-starter h1,
  .hero-main h1,
  .hero-dessert h1,
  .hero-pies h1 {
    grid-column: 2 / 4;
    grid-row: 1;
  }

  /* .hero-home h1,
  .hero-starter h1,
  .hero-main h1,
  .hero-dessert h1,
  .hero-pies h1 {
    font-size: 2vw;
  }*/

  .container-1 h2,
  .container-2 h2,
  .container-3 h2,
  .container-4 h2 {
    color: #ffffff;
    text-align: left;
    background-color: #000000;
    opacity: 70%;
    font-size: 1em;
    padding: 0.1em;
  }

  .container-1 {
    grid-column: 2 / 4;
    grid-row: 2;
  }

  .container-2 {
    grid-column: 2 / 4;
    grid-row: 3;
    margin-top: 2em;
  }

  .container-3 {
    grid-column: 2 / 4;
    grid-row: 4;
    margin-top: 1em;
  }

  .container-4 {
    grid-column: 2 / 4;
    grid-row: 5;
    margin-top: 2em;
    margin-bottom: 2em;
  }

  .ingredients {
    grid-column: 2 / 4;
    grid-row: 3;
    margin: 1em 0 1em 0;
  }

  .ingredients h2 {
    margin: 0.5em 0 0 0;
  }

  .ingredients-list {
    margin: 0;
  }
  .method {
    grid-column: 2 / 4;
    grid-row: 4;
    margin: 1em 0 1em 0;
  }

  .method h2 {
    margin: 0;
  }

  .method-list {
    margin: 0;
  }
}

body {
  font-family: "MuseoModerno", cursive;
}

nav {
  background: #b5c723;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav ul {
  display: flex;
  list-style-type: none;
}

nav ul li {
  padding-right: 2em;
}

nav ul li a {
  text-decoration: none;
  text-align: center;
  font-weight: bold;
  font-size: 1.125rem;
  color: #000000;
  padding: 0.5em 0.75em;
}

nav #current {
  color: #000000;
  border: solid #000000;
  border-radius: 0.3125em;
}

nav ul li a:hover {
  color: #000000;
  text-decoration: none;
}

/*double check this*/
nav ul li a.active {
  color: #000000;
  text-decoration: none;
  border: solid #000000;
  border-radius: 10px;
}

nav ul li a:active {
  color: #000000;
  text-decoration: underline;
}

@media screen and (min-width: 360px) and (max-width: 875px) {
  nav ul {
    display: flex;
    flex-direction: column;
    line-height: 2;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 390px) {
  nav ul {
    flex-direction: column;
    line-height: 2;
  }
}

